@charset "UTF-8";
/* ================================
   app.scss (ARCHIVO PRINCIPAL)
   ================================ */
/* Importa el archivo normalize:
   - Sirve para “normalizar” estilos entre navegadores
   - Evita diferencias visuales entre Chrome, Firefox, Edge, etc. */
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/* Importa las variables globales del proyecto:
   - Fuentes, colores, breakpoints, separaciones, etc. */
/* ================================
   VARIABLES GLOBALES (SCSS)
   ================================ */
/* Importa la fuente "Lato" desde Google Fonts
   - Incluye los pesos: 300, 400, 700 y 900 */
/* Fuente principal del proyecto
   - Se usará en la mayoría del sitio (body, títulos, etc.) */
/* Breakpoint para teléfonos (pantallas pequeñas) */
/* Breakpoint para tablets (pantallas medianas) */
/* Breakpoint para escritorio (pantallas grandes) */
/* Colores base del proyecto */
/* Separación general del diseño (espaciados y márgenes) */
/* Pesos de tipografía disponibles según la fuente importada */
/* Importa mixins reutilizables:
   - Media queries (telefono, tablet, desktop)
   - Grid u otras ayudas */
/** Media Queries **/
/* ================================
   MIXINS (REUTILIZABLES)
   ================================ */
/** Media Queries **/
/* Mixin para estilos desde tamaño "teléfono" en adelante
   - Usa la variable $telefono como breakpoint mínimo
   - @content permite insertar estilos dentro del mixin */
/* Mixin para estilos desde tamaño "tablet" en adelante
   - Usa la variable $tablet como breakpoint mínimo */
/* Mixin para estilos desde tamaño "desktop" en adelante
   - Usa la variable $desktop como breakpoint mínimo */
/* Mixin para crear un grid de forma rápida y parametrizable
   - $columnas: número de columnas del grid
   - $espaciado: separación (gap) entre filas y columnas */
/* Mixin pa botones asignado a los anuncios en el index.html

*/
/* Importa los estilos globales:
   - body, h1-h4, p, imágenes, contenedor, etc. */
/* ================================
   ESTILOS GLOBALES (BASE)
   ================================ */
/* Ajusta el tamaño base de la tipografía:
   62.5% equivale a 10px aprox (si el navegador usa 16px por defecto)
   Esto facilita trabajar con rem: 1rem = 10px */
html {
  font-size: 62.5%;
  /* Hace que el padding y border se incluyan dentro del ancho/alto total */
  box-sizing: border-box;
}

/* Aplica box-sizing heredado a todos los elementos y pseudoelementos
   para tener medidas más predecibles en todo el proyecto */
*, *:before, *:after {
  box-sizing: inherit;
}

/* Estilos generales del body (texto global del sitio) */
body {
  /* Fuente principal definida como variable en SCSS */
  font-family: "Lato", sans-serif;
  /* Tamaño base del texto general */
  font-size: 1.6rem;
  /* Altura de línea para mejorar legibilidad */
  line-height: 1.8;
}

/* Estilos por defecto para los párrafos */
p {
  /* Color del texto usando variable SCSS */
  color: #000000;
  /* Tamaño de letra para párrafos */
  font-size: 2rem;
}

/* Clase contenedor:
   Se usa para centrar el contenido y limitar el ancho máximo */
.contenedor {
  /* Ocupa el 95% del ancho disponible */
  width: 95%;
  /* Limita el ancho máximo en pantallas grandes */
  max-width: 120rem;
  /* Centra el contenedor automáticamente (izquierda y derecha) */
  margin: 0 auto;
}

/* Estilos generales para enlaces */
a {
  /* Quita el subrayado típico de los links */
  text-decoration: none;
}

/* Ajusta imágenes y picture para que sean responsivos */
img,
picture {
  /* Ocupan el 100% del contenedor */
  width: 100%;
  /* Evita espacios raros debajo de las imágenes (display inline por defecto) */
  display: block;
}

/* Estilos generales para títulos h1, h2 y h3 */
h1, h2, h3 {
  /* Fuente principal del proyecto */
  font-family: "Lato", sans-serif;
  /* Margen vertical dinámico basado en la variable de separación */
  margin: 2.5rem 0;
  /* Peso de letra más liviano para una apariencia moderna */
  font-weight: 300;
  /* Centra el texto */
  text-align: center;
}

/* Tamaños específicos para cada encabezado */
h1 {
  font-size: 3.8rem; /* Título principal */
}

h2 {
  font-size: 3.4rem; /* Subtítulo importante */
}

h3 {
  font-size: 3rem; /* Subtítulo secundario */
}

h4 {
  font-size: 2.6rem; /* Título más pequeño */
}

/* Importa utilidades generales:
   - clases como .seccion, .contenido-centrado, etc. */
/* ================================
   UTILIDADES (CLASES REUTILIZABLES)
   ================================ */
/* .seccion:
   Se usa para dar separación vertical estándar entre secciones del sitio
   - Aplica margen superior e inferior usando la variable $separacion */
.seccion {
  /* Margen superior con la mitad del valor definido en $separacion */
  margin-top: 2.5rem;
  /* Margen inferior con la mitad del valor definido en $separacion */
  margin-bottom: 2.5rem;
}

/* .contenido-centrado:
   Sirve para limitar el ancho del contenido y hacerlo más legible
   (útil para textos largos, artículos, secciones de lectura) */
.contenido-centrado {
  /* Define un ancho máximo fijo para que no se estire demasiado en pantallas grandes */
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

/* importa estilos asociados a los botones:
   - estilo boton de anuncios*/
.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.boton-amarillo:hover {
  background-color: rgb(199.4849785408, 120.2253218884, 8.0150214592);
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.boton-verde:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
}

/** ================================
    UI (ESTILOS DE LA INTERFAZ)
    ================================ **/
/* Importa el layout del header:
   - Fondo, imagen, títulos, estructura del encabezado */
/* ================================
   HEADER (ENCABEZADO)
   ================================ */
/* Cuando el navegador NO soporta WebP:
   se aplica la imagen JPG como fondo en la clase .inicio */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

/* Cuando el navegador SÍ soporta WebP:
   se aplica la imagen WebP como fondo en la clase .inicio */
.webp .inicio {
  background-image: url(../img/header.webp);
}

/* Estilos generales del header */
.header {
  /* Color de fondo por defecto (útil mientras carga la imagen o en otras páginas) */
  background-color: #333333;
  /* Padding: 1rem arriba, 0 laterales, 3rem abajo */
  padding: 1rem 0 3rem 0;
  /* &.inicio significa: .header.inicio
     Se aplica solo cuando el header tenga ambas clases */
}
.header.inicio {
  /* Centra la imagen de fondo */
  background-position: center center;
  /* Hace que la imagen cubra todo el espacio disponible */
  background-size: cover;
  /* Desde tablet en adelante se define una altura mínima */
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio {
  /* Estilos específicos para el contenedor interno del header */
}
.header.inicio .contenido-header {
  /* En tablet también se fuerza el alto mínimo
     (sirve para alinear bien el contenido con flex) */
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

/* Contenido interno del header (texto, navegación, etc.) */
.contenido-header {
  /* Flexbox para organizar el contenido de forma vertical */
  display: flex;
  /* Elementos en columna (uno debajo del otro) */
  flex-direction: column;
  /* Distribuye el contenido dejando espacio entre el top (barra) y el h1 */
  justify-content: space-between;
  /* Estilos del título principal dentro del header */
}
.contenido-header h1 {
  /* Alinea el texto a la izquierda */
  text-align: left;
  /* Color blanco para contraste con el fondo */
  color: #FFFFFF;
  /* Peso del texto usando variable SCSS */
  font-weight: 700;
  /* Espaciado vertical entre líneas */
  line-height: 1.5;
  /* Limita el ancho para mejor lectura y estética */
  max-width: 60rem;
}

/* Importa el layout del footer:
   - Fondo, navegación del footer, copyright, etc. */
/* ================================
   FOOTER (PIE DE PÁGINA)
   ================================ */
/* Contenedor principal del footer */
.footer {
  /* Espaciado interno (arriba y abajo):
     - $separacion / 2 define un padding proporcional al sistema de espaciado del proyecto
     - 0 indica que no hay padding lateral */
  padding: 2.5rem 0;
  /* Color de fondo del footer usando variable SCSS */
  background-color: #333333;
  /* Contenedor interno del footer (generalmente para alinear contenido y navegación) */
}
.footer .contenedor-footer {
  /* Espaciado superior adicional para separar el contenido dentro del footer */
  padding-top: 2.5rem;
}
.footer {
  /* Texto de derechos reservados */
}
.footer .copyright {
  /* Color blanco para contrastar con el fondo oscuro */
  color: #FFFFFF;
}

/* Importa el layout de navegación:
   - Estilos de la barra y links del menú */
/* ================================
   BARRA (LOGO + NAVEGACIÓN)
   ================================ */
/* .barra:
   Contenedor que normalmente incluye el logo y el menú de navegación */
.barra {
  /* Activa Flexbox para alinear elementos */
  display: flex;
  /* En móvil: se organizan en columna (uno debajo del otro) */
  flex-direction: column;
  /* Separación superior para dar aire al header */
  padding-top: 3rem;
  /* Desde tablet en adelante cambia la distribución */
}
@media (min-width: 768px) {
  .barra {
    /* En pantallas medianas/grandes: elementos en fila (lado a lado) */
    flex-direction: row;
    /* Se separan al máximo:
       logo a la izquierda y menú a la derecha */
    justify-content: space-between;
  }
}

/* ================================
   NAVEGACIÓN (MENÚ)
   ================================ */
/* .navegacion:
   Contenedor de los links del menú */
.navegacion {
  /* Desde tablet en adelante ajusta la disposición del menú */
}
@media (min-width: 768px) {
  .navegacion {
    /* Los enlaces se alinean horizontalmente */
    display: flex;
    /* Alinea verticalmente los links al centro */
    align-items: center;
  }
}
.navegacion {
  /* Estilos para cada link dentro de .navegacion */
}
.navegacion a {
  /* Color blanco para buen contraste sobre el header */
  color: #FFFFFF;
  /* Quita el subrayado por defecto */
  text-decoration: none;
  /* Tamaño de letra más grande en móvil */
  font-size: 2.2rem;
  /* Hace que el link sea un bloque (más fácil de tocar en móvil) */
  display: block;
  /* Centra el texto en móvil */
  text-align: center;
  /* Ajustes específicos desde tablet */
}
@media (min-width: 768px) {
  .navegacion a {
    /* Espaciado entre links */
    margin-right: 2rem;
    /* Reduce un poco el tamaño para pantallas grandes */
    font-size: 1.8rem;
    /* Al último enlace le quita el margen derecho
       para evitar espacio extra al final */
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/*Importa el layout de los iconos:
  -etilos iconos y contenido iconosdel index.html*/
@media (min-width: 768px) {
  .iconos-nosotros {
    /* Activa CSS Grid en el contenedor */
    display: grid;
    /* Espacio entre columnas y filas */
    gap: 2rem;
    /* Define la cantidad de columnas:
       repeat($columnas, 1fr) crea columnas del mismo tamaño */
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono { /*llamamos la clase icono del index.html*/
  text-align: center; /* Centra el texto */
}
.icono img { /*etiqueta html img*/
  height: 10rem; /*tamaño de la imagen*/
}
.icono h3 { /*etiqueta html h3*/
  text-transform: uppercase; /*Transforma el texto del h3 en mayusculas*/
}

/*Importa el layout de los anuncios del index.html*/
@media (min-width: 768px) {
  .contenedor-anuncios {
    /* Activa CSS Grid en el contenedor */
    display: grid;
    /* Espacio entre columnas y filas */
    gap: 2rem;
    /* Define la cantidad de columnas:
       repeat($columnas, 1fr) crea columnas del mismo tamaño */
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(199.5, 199.5, 199.5);
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio .precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
  font-size: 1.4rem;
}